import React from 'react';

const InputBox = () => {
    return (
        <div id="inputbox">
            <h2>List of candidate items:</h2>
            <textarea></textarea>
        </div>
    )
}

const Control = () => {
    return (
        <div id="control">
            <button>Load</button>
            <button>Start</button>
            <button>Stop</button>
        </div>
    )
}

const Item = props => <div className="item">{props.itemName}</div>
const itemList = [
    'A','B','C','D','E','F','G','H','I','J','Item with longer name',
];

const RouletteItems = props => {
    const items = props.itemList.map(itemLabel => {
        return <div class="item">{itemLabel}</div>
    });
    return items;
}

const Display = () => {
    return (
        <div className="container">
            <RouletteItems itemList={itemList} />
        </div>
    )
}

const App = () => (
    <div>
        <InputBox/>
        <Control/>
        <Display/>
    </div>
)
export default App;